import React from "react";
import Game from "./game";
import "./style.css"

export default class App extends React.Component {

state = {
  p: 0,
  t: 0,
  c: 0,
  r: 0
}

  result = (p,c) => {

    this.setState({
      r: this.state.r+1
    })
    
      if(p==1 & c==2) {
        this.setState({
          c: this.state.c+1
        })
      }
      else if(p==c) {
        this.setState({
          t: this.state.t+1
        })
      }
      else if(p==2 & c==1) {
        this.setState({
          p: this.state.p+1
        })
      }
      else if(p==1 & c==3) {
        this.setState({
          p: this.state.p+1
        })
      }
      else if(c==1 & p==3)
      {
        this.setState({
          c: this.state.c+1
        })
      }
      else if(p==2 & c==3) {
        this.setState({
          c: this.state.c+1
        })
      }
      else {
        this.setState({
          p: this.state.p+1
        })
      }

      if(this.state.r==5) {
        this.setState({
          c: 0,
          t: 0,
          r: 0,
          p: 0
        })
      }
  }
  
  render() {
    return(
      <div>
      <h1>Round {this.state.r}</h1>
      <div className="row">
      <div className="col-sm-4">
      <h2>Win</h2>
      <div><h3>{this.state.p}</h3></div>
      </div>
      <div className="col-sm-4">
      <h2>Tie</h2>
      <div><h3>{this.state.t}</h3></div>
      </div>
      <div className="col-sm-4">
      <h2>Win</h2>
      <div><h3>{this.state.c}</h3></div>
      </div>
      </div>
       <Game 
       result = {this.result}
       r = {this.state.r}
       p = {this.state.p}
       c= {this.state.c}
       />
      </div>
    );
  }
}

