import React from "react";

export default class Result extends React.Component {

    render() {
        
    if(this.props.p > this.props.c) {
        return(<h1>you won!</h1>);
    }
    else if(this.props.p < this.props.c) {
       return( <h1>Try Your Luck Again!</h1> );
    }
    else {
        return( <h1>Tie!</h1> );
    } 
}
}